import React from "react";
import Navbar from "../components/Navbar";
import Review from "../components/Review";
import "../styles/global.css";
import logoImage from "../images/Max.jpg"; // Updated to Max.jpg

export default function Home() {
  return (
    <div className="min-h-screen flex flex-col bg-pastel-lavender text-white overflow-y-auto">
      <Navbar />
      <header className="bg-pastel-lavender/30 py-24 px-6 text-center border-2 border-white/20 rounded-b-lg">
        <div className="flex justify-center items-center flex-col">
          <h1
            className="text-4xl sm:text-6xl md:text-8xl font-cursive mb-4 text-center transform hover:scale-105 transition duration-300 hover-glow text-gray-900"
            style={{ textShadow: "2px 2px 4px rgba(0, 0, 0, 0.3)" }}
          >
            One<u>L</u>ove
          </h1>
          <h2 className="text-2xl sm:text-2xl md:text-3xl mb-8 font-light text-gray-900">
            Mobile Grooming
          </h2>
          <p className="text-lg sm:text-lg md:text-xl mb-8 text-gray-700">
            Cutting-edge grooming at your doorstep
          </p>
          <img
            src={logoImage}
            alt="Logo"
            className="w-64 sm:w-96 md:w-[32rem] mx-auto my-4 rounded-lg shadow-md"
          />
          <a
            href="/services"
            className="inline-block bg-white text-pastel-lavender font-semibold py-3 px-6 rounded-full shadow-lg hover:bg-gray-200 hover-glow transition"
          >
            <strong>Explore Our Services</strong>
          </a>
        </div>
      </header>
      {/* Placeholder Section Removed (Moved to Services) */}
      {/* Decorative Clip (Paw Print) */}
      <div className="text-center my-6">
        <svg
          className="w-12 h-12 mx-auto text-pastel-lavender"
          fill="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-1 17.93c-3.95-.49-7-3.85-7-7.93 0-.62.08-1.21.21-1.79L9 14v1c0 1.1.9 2 2 2v1.93zm6.9-2.54c-.26-.81-1-1.39-1.9-1.39h-1v-3c0-.55-.45-1-1-1H8v-2h2c.55 0 1-.45 1-1V7h2c1.1 0 2-.9 2-2v-.41c2.93 1.19 5 4.06 5 7.41 0 2.08-.8 3.97-2.1 5.39z" />
        </svg>
      </div>
      <Review />
      <section className="py-16 px-6 bg-pastel-lavender/30 text-pastel-lavender text-center border-2 border-white/20 rounded-lg">
        <h2 className="text-4xl font-semibold mb-6">
          <u>Book Your Appointment</u>
        </h2>
        <p className="text-lg italic">Booking system coming soon!</p>
      </section>
    </div>
  );
}